<template>
    <div class="categories-view" v-if="Object.keys(pageContent).length">
        <page-title :title="pageContent.pageTitle[lang]"/>
        <div class="wrapper">
            <div class="cat-search-field">
                <input type="text" :placeholder="pageContent.searchFieldText[lang]" v-model="query">
            </div>
            <div class="categories-list">
                <div class="flex-row-12" v-if="filteredCategories.length">
                    <div class="flex-col xs-12 sm-6 md-4" v-for="(category, i) in filteredCategories" :key="i">
                        <category-card :category="category"/>
                    </div>
                </div>
                <div class="msg" v-else>
                    <span class="fas fa-sad-tear"></span>
                    <p>{{ pageText.emptyCatalogMsg[lang] }}</p>
                </div>
            </div>
        </div>
        <questions-form />
    </div>
</template>

<script>
import QuestionsForm from '../components/QuestionsForm.vue';
import PageTitle from '../components/PageTitle.vue';
import CategoryCard from '../components/CategoryCard.vue';

export default {
    data() {
        return {
            query: '',
            pageText: {
                emptyCatalogMsg: {
                    ua: 'Вибачте, але ми не можемо знайти категорії по Вашому запиту',
                    ru: 'Извините, но мы не можем найти категории по Вашему запросу',
                    en: 'Sorry, but we cant find any categories by your request'
                }
            }
        }
    },  
    computed: {
        lang() {
            return this.$store.getters['getSelectedLang'];
        },
        pageContent() {
            return this.$store.getters['getCatalogPageContent'];
        },
        categories() {
            return this.$store.getters['getCategories'];
        },
        filteredCategories() {
            return (this.query.length)
                ? this.categories.filter(el => {
                    return ~el.name['ua'].toLowerCase().indexOf(this.query.toLowerCase())
                        || ~el.name['ru'].toLowerCase().indexOf(this.query.toLowerCase())
                        || ~el.name['en'].toLowerCase().indexOf(this.query.toLowerCase())
                })
                : this.categories;
        }
    },
    components: {
        QuestionsForm,
        PageTitle,
        CategoryCard
    }
}
</script>